body {
  min-height: 100vh;
  background: linear-gradient(135deg, #77767e 0%, #7a3838 100%);
  color: #ffffff;
  margin: 0;
  padding: 0px;
  font-family: sans-serif; /* Optional: Set a default font */
  display: flex; 
  flex-direction: column;
}

main {
  padding: 20px;
  flex: 1; /* Allow main to take up remaining vertical space */
}

footer {
  text-align: center;
  padding: 10px;
  margin-top: auto; /* Push the footer to the bottom */
}